import InfoPageTemplate from "pages/InfoPage/InfoPage";

function LegendsPage() {
    return (
        <>
            <InfoPageTemplate title="Легенды мира" audioCatch={[0, 125, 310]} audio={new Audio("./static/legends.mp3")} options={[{id: 1, displayValue: "Дар Ванов и появления Фальтов"}, {id: 2, displayValue: "Подвиг Свена отважного "}, {id:3, displayValue:"Сказание о Фальте и Страже"}]} >
                <div>
                    <h2 id="l1" style={{textAlign: "center", fontSize: "60px", margin: "0"}}>*** Дар Ванов и появления Фальтов ***</h2>
                    <p style={{fontSize: "30px"}}>
                        Эта ночь была поистине волшебной, ведь сегодня по тропе шли не просто путники, а группа из шести Ванов. Они были словно из древних сказок: их кожа была зеленоватого оттенка, а уши длинны и остры, что сильно отличало их от людей. Они шли, чтобы спасти Мидгард от великой зимы, которая должна была длиться три года. Их путь в поселение людей провожал свет благородной луны. По приходу в поселение, люди встретили богов со всем почтением. Ваны были из далёких земель вечно цветущего леса, где никогда не бывает зимы и всегда весна. Благородные Ваны пришли обучить достойных людей магии, но были и те, чьи сердца были черны и полны зависти. Им было очень обидно, что они были лишены почестей Ванов, и они решили нарушить все принципы и предать род человеческий, отправившись в страну вечной мерзлоты и пустынных гор, Ётунхейм. Там жили мерзкие и полные злобы Ётуны, Великаны, чья кожа была похожа на глыбы льда, а волосы на снежные вершины, такие же белоснежные и холодные. Они были безобразны, словно бесформенные подобия людей из снега и льда. Предатели рода людского вступили в преступный союз с этими монстрами, чтобы заполучить их магию. Но если магия Ваннов была красивой, дарующей жизнь и имела высокую духовную связь с природой, то магия Ётунов была такая же мерзкой и холодной, как и сами её хозяева. Так на свет появились Фальты, наполовину люди, а наполовину Ётуны. Они были больше похожи на людей, но их высокий рост и белые волосы легко выдавали их происхождение.
                    </p>
                    <h2 id="l2" style={{textAlign: "center", fontSize: "60px", margin: "0"}}>*** Подвиг Свена отважного ***</h2>
                    <p style={{fontSize: "30px"}}>
                        Десять лет назад мы с отрядом вышли в самую жуткую и холодную ночь. Мы чувствовали себя, будто в Ётунхейме, где не было надежды и даже чуточка тепла! Была сильная метель, которая могла замести целый город! Но мы не могли повернуть назад, ведь Хельмор, исчадие Фальтов, некромант подобрался слишком близко к нашему поселению! Чтобы избежать катастрофы я, твой отец и ещё два смельчака вышли на охоту! Мы долго шли по кровавому следу этого отродья, везде можно было увидеть следы его жутких преступлений! От которых даже бывалых охотников могло пробрать до костей! Но мы шли, не испытывая страха и не озираясь назад. Вскоре мы наконец нашли убежище твари — огромная пещера и ужасный смрад разлагающейся плоти. Он был настолько ужасен, что мог вырубить даже мамонта! Вокруг пещеры было множество трупов людей и животных! Зрелище ужасное и зловещее! Мы приготовились к битве! Внезапно метель усилилась настолько, что мы не могли разглядеть даже своих ног! И тут мы поняли, что попали в западню, и из-за сильной метели мы разделились по двое. Я был с твоим отцом, мы были полны решимости! Вдруг тела, что лежали и разлагались, начали оживать! Они появлялись со всех сторон, я и Свен встали спина к спине и отражали все атаки живых мертвецов. В один момент меня сильно ранили и я не смог продолжать сражение, тогда Свен героически стал защищать меня и сражаться совершенно в одиночку! Он изрубил более сотни мертвецов, и когда метель развеялась, мы отправились за головой твари! Мы медленно и не спеша зашли в пещеру, ужасный смрад мертвечины здесь был невыносим, в наших глазах была ярость и решимость! Мы готовы были уничтожить это отродье зла! Пещера была огромной, но без туннелей и развилок. По-видимому, Хельмор прятался тут достаточно долго и хорошо обжился здесь. Мы слышали жуткое чавканье, которое исходило из глубины пещеры. Мы шли на звук, очень осторожно и скрытно! В один из моментов мы увидели исполина, огромного и мерзкого. Он выглядел почти как Ётун, такой же безобразный и синий, как глыба льда. Он поедал труп так увлеченно, что даже не заметил нас! Твой отец и мой сын сделал решительный рывок в сторону чудовища и одним взмахом отрубил ему голову! Теперь эта история упоминается как подвиг Свена отважного! или истребителя нежити!
                    </p>
                    <h2 id="l3" style={{textAlign: "center", fontSize: "60px", margin: "0"}}>*** Сказание о Фальте и Страже ***</h2>
                    <p style={{fontSize: "30px"}}>
                        Было это одной из самых тёмных ночей, её ещё называют ночью Норнов. Говорят, в эту ночь Норны делают предсказания и решают судьбу всего живого! Может быть, это и стало причиной такого необычного расклада событий. В эту самую волшебную ночь, на охоту вышли стражи пустоши, они выследили Фальта и шли по его следу, чтобы погубить. Из-за ледяного тумана, что пробирал своим холодом даже троллей, охотники плохо видели. Но их вела чуйка и опыт. Они шли, нагоняя своего врага. Один из них отбился от отряда и оказался посреди гигантского замершего озера, он шёл аккуратно и не спеша. Лёд трещал под его шагами. Вдруг произошёл сильный треск. Лёд стал стремительно расходиться и образовывать глубокую впадину в саму бездну. На лице стража был сильный страх, он не мог и подумать, что погибнет не в бою, а так. Он не мог смириться с тем, что не видать ему чертогов Одина! Собравшись, он успел выхватить меч и вонзить его в глыбу льда, дабы не упасть в бездну. Он думал: "Я не могу так умереть! Только в бою я приму благородную смерть!"
                    </p>
                    <p style={{fontSize: "30px"}}>
                        Оставаясь на волоске от смерти, он хватался за жизнь! Но меч предательски стал соскальзывать, потихонечку и мучительно! Но вдруг очень сильная рука схватила его и вытащила. Он понимал, что перед ним его враг, Фальт! Но он не мог поверить, что это чудовище спасло его. Зачем, и почему? Думал охотник. Когда он поднял глаза, он очень удивился! Это была прекрасная девушка, с белоснежными волосами и нежной голубоватой кожей! Она была прекрасна и неповторима, словно самая прелестная снежинка, спустившаяся с самых небес. Кодекс стражей пустоши требовал убивать всех Фальтов. Но его честь не позволила бы сделать такое с своим спасителем и тем более девушкой!
                    </p>
                    <p style={{fontSize: "30px"}}>
                        Он спрятал, увёл свою спасительницу от охотников. А также навеки пропал сам. Среди Фальтов говорят, что он остался охранять и заботиться о своей снежинке.
                    </p>
                </div>
            </InfoPageTemplate>
        </>
    )
}

export default LegendsPage;